
<template>
    <div class="container">
      
      <form @submit.prevent="addEleve" class="row form-form form-75">

        <div class="title">

              <h4>
                <span>Inscription</span> <span>Elve</span>
              </h4>

              <div class="line">
                    
              </div>
        </div>

        <h5>Informations  Elve</h5>
        <div class="col-12   col-md-6">
          <b-form-group label="nom :">
            <b-form-input id="nom" v-model="new_eleve.nom" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
                {{new_eleve.nom_error}}
          </div>
        </div>

        <div class="col-12 col-md-6">
          <b-form-group label="Prnom :">
            <b-form-input id="prenoms" v-model="new_eleve.prenoms" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
                {{new_eleve.prenoms_error}}
          </div>
        </div>
      
        <div class="col-12">
          <b-form-group label="Date Naissance :">
            <b-form-input id="date_naissance" v-model="new_eleve.date_naissance" type="date" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
                {{new_eleve.date_naissance_error}}
          </div>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Ecole :">
            <b-form-select @change="handleEcoleChange" v-model="new_eleve.ecole" :options="ecoles" required></b-form-select>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.ecole_error}}
          </div>
        </div>

        <div class="row col-12 col-md-6" v-if="!new_eleve.create_classe">
            <div class="col-10">
               <b-form-group label="Classe :">
               <b-form-select v-model="new_eleve.classe" :options="classes_filter" required></b-form-select>
              </b-form-group>
              <div class="text-danger">
                  {{new_eleve.classe_error}}
              </div>
            </div>
            <div class="col-2">
                <input v-model="new_eleve.create_classe" type="checkbox">
                <label for="">Autre Classe</label>
            </div>
        </div>

        <div v class="row col-12 col-md-6" v-if="new_eleve.create_classe">
          <div class="col-10">
             <b-form-group label="Nouvel classe :">
                 <b-form-input v-model="new_eleve.new_classe.nom" required></b-form-input>
            </b-form-group>
            <div class="text-danger">
                  {{new_eleve.classe_error}}
            </div>
          </div>
          <div class="col-2">
              <input v-model="new_eleve.create_classe" type="checkbox">
              <label for="">Autre Classe</label>
          </div>
      </div>

        <div class="col-12 col-md-6">
            <b-form-group label="Image :">
              <input type="file" accept="image/*" @change="handleImageUploadNewEleve">
            </b-form-group>
            <div class="text-danger">
              {{ new_eleve.image_error }}
            </div>
        </div> 

        <div class="col-12">
          <b-form-group label="Adresse :">
            <b-form-input v-model="new_eleve.adresse" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.adresse_error}}
          </div>
        </div>

        <div class="col-12 col-md-6">
          <b-form-group label="Ligne de bus :">
            <b-form-select v-model="new_eleve.ligne" @change="handleLigneChange" :options="lignes" required></b-form-select>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.ligne_error}}
          </div>
        </div>

        <div class="col-12 col-md-6">
          <b-form-group label="Lieu de ligne :">
            <b-form-select @change="handleLieuLigneChange"  v-model="new_eleve.lieuligne" :options="lieulignes_filter" required></b-form-select>
          </b-form-group>
            <div class="text-danger">
                {{new_eleve.lieuligne_error}}
            </div>
        </div>

      



        <div class="col-12 col-md-12">
          <b-form-group label="Type Inscription :">
            <b-form-select   v-model="new_eleve.type_inscription" :options="type_inscriptions" required></b-form-select>
          </b-form-group>
          <div class="text-danger">
                {{new_eleve.lieuligne_error}}
          </div>
        </div>

        <!-- ramassage -->
        <template v-if="new_eleve.type_inscription === 'ramasse et remisage' || new_eleve.type_inscription === 'ramassage'">
     
            <div class="row" v-if="!new_eleve.create_lieu_ramassage">
                      <div class="col-10" >
                            <b-form-group label="Lieu de ramassage :">
                              <b-form-select v-model="new_eleve.lieu_ramassage" :options="lieu_ramassages_filter"></b-form-select>
                            </b-form-group>
                            <div class="text-danger">
                                 {{ new_eleve.lieu_ramassage_error }}
                            </div>
                      </div>
                      <div class="col-2 checkbox" >
                            <input type="checkbox" v-model="new_eleve.create_lieu_ramassage" >
                            <label for="">
                                Autre Lieu de ramassage au lieu
                            </label>
                      </div>
                  </div>
      
                  <div class="row" v-if="new_eleve.create_lieu_ramassage">
                      <div class="col-11 checkbox">
                          <b-form-group label="Nouveau Lieu de Ramassage :">
                            <b-form-input id="Lieu de Ramassage " v-model="new_eleve.new_lieu_ramassage.nom_lieu" required></b-form-input>
                          </b-form-group>
                      </div>
                      <div class="col-1 checkbox" >
                        <input type="checkbox" v-model="new_eleve.create_lieu_ramassage" >
                        <label for="">
                               Autre Lieu de ramassage au lieu
                        </label>
                  </div>
              </div>
           <!-- Code to be displayed when inscription is 'ramassage' or 'remisage' -->
      </template>

      <div class="text-danger">
          {{errors.type_inscription}}
      </div>
      
      <template v-if="new_eleve.type_inscription === 'ramasse et remisage' || new_eleve.type_inscription === 'remisage'">
              <!-- Lieu Remisage -->
              <div class="row" v-if="!new_eleve.create_lieu_remisage">
                    <div class="col-10" >
                        <b-form-group label="Lieu de remisage :">
                          <b-form-select v-model="new_eleve.lieu_remisage" :options="lieu_ramassages_filter"></b-form-select>
                        </b-form-group>
                        <div class="text-danger">
                          {{ new_eleve.lieu_ramassage_error }}
                        </div>
                      </div>
                      <div class="col-2 checkbox" >
                          <input type="checkbox" v-model="new_eleve.create_lieu_remisage" >
                            <label for="">
                                Autre Lieu de Remisage
                            </label>
                      </div>
              </div>
            
              <div class="row" v-if="new_eleve.create_lieu_remisage">
                  <div class="col-11 checkbox">
                      <b-form-group label="Nouveau Lieu de Remisage :">
                        <b-form-input id="Lieu de Remisage " v-model="new_eleve.new_lieu_remisage.nom_lieu" required></b-form-input>
                      </b-form-group>
                  </div>
                  <div class="col-1 checkbox" >
                    <input type="checkbox" v-model="new_eleve.create_lieu_remisage" >
                    <label for="">
                      Autre Lieu de remisage 
                    </label>
                   </div>
              </div>

        </template>

         <!-- Itineraire Bus  -->
         <div class="table-responsive">
          <table class="table">
              <thead>
                <tr>
        
                  <th>Cocher</th>
                  <th>Lieu Dpart</th>
                  <th>Lieu Arrive</th>
                  <th>Heure Dpart</th>
                  <th>Heure d'arrivi</th>

                </tr>
              </thead>
              <transition-group name="itineraire-table" tag="tbody">
                <tr v-for="itineraire in itineraires_filter" :key="itineraire.id">


                  <td>
                       <input type="checkbox" name="" @input="handleChangeItineraireCheckBox(itineraire.pk)" id="">
                  </td>
                  <td v-if="itineraire.lieuDepart">{{ itineraire.lieuDepart.repr }}</td>
                  <td v-if="!itineraire.lieuDepart">------------</td>
                  <td v-if="itineraire.lieuArrivee">{{ itineraire.lieuArrivee.repr }}</td>
                  <td v-if="!itineraire.lieuArrivee">-------------</td>
                  <td>{{ itineraire.heureDepart }}<template v-if="!itineraire.heureDepart">-------------</template></td>
                  <td>{{ itineraire.heureArrivee }}<template v-if="!itineraire.heureArrivee">-------------</template></td>
                
                  <td>
                    
                  </td>
                </tr>
              </transition-group>
          </table>
        </div>


        <h5>Informations Parent Elve</h5>
      <div class="col-12 col-md-6">
          <b-form-group label="Nom Pre :">
            <b-form-input v-model="new_eleve.parent.nom_pere" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.parent.nom_pere_error}}
          </div>
      </div>
      <div class="col-12 col-md-6">
      
          <b-form-group label="Tel :">
            <b-form-input v-model="new_eleve.parent.tel_pere" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.parent.tel_pere_error}}
          </div>
      </div>

      <div class="col-12 col-md-6">
          <b-form-group label="Nom Mre :">
            <b-form-input v-model="new_eleve.parent.nom_mere" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.parent.nom_mere_error}}
          </div>
      </div>

      <div class="col-12 col-md-6">
        <b-form-group label="Tel :">
          <b-form-input v-model="new_eleve.parent.tel_mere" required></b-form-input>
        </b-form-group>
        <div class="text-danger">
          {{new_eleve.parent.tel_mere_error}}
        </div>
      </div>
      <template v-if="!$store.state.user">
      <h5>Informations Compte d'utilisateur</h5>
      <div class="col-12 col-md-6">
          <b-form-group label="Nom d'utilisateur :">
            <b-form-input v-model="new_eleve.user.username" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
          </div>
      </div>


      <div class="col-12 col-md-6">
        <b-form-group label="Email :">
              <b-form-input v-model="new_eleve.user.email" type="email" required></b-form-input>
            </b-form-group>
            <div class="text-danger">
        
            </div>
      </div>


      <div class="col-12 col-md-6">
        <b-form-group label="Mots de passe :">
          <b-form-input v-model="new_eleve.user.password" type="password" required></b-form-input>
        </b-form-group>
        <div class="text-danger">
        </div>
      </div>


      <div class="col-12 col-md-6">
          <b-form-group label="Verification mots de passe :">
            <b-form-input v-model="new_eleve.user.password_verify"  type="password" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.user.password_error}}
          </div>
      </div>

    </template>
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from '../apiService';

  export default {
    name: 'EleveCreatePage',
    props: {
         baseUrl: String
    },
    watch: {
      'new_eleve.ligne': function(newValue, oldValue) {
        oldValue
           this.new_eleve.new_lieu_ramassage.ligne=newValue;
           this.new_eleve.new_lieu_remisage.ligne=newValue
           this.filterItineraire()
      },
      'new_eleve.ecole': function(newValue, oldValue) {
        oldValue
        newValue
          //  this.new_eleve.new_lieu_ramassage.ligne=newValue;
          //  this.new_eleve.new_lieu_remisage.ligne=newValue
           this.filterItineraire()
      },
      'new_eleve.lieuligne': function(newValue, oldValue) {
        oldValue
           this.new_eleve.new_lieu_ramassage.lieu_ligne=newValue
           this.new_eleve.new_lieu_remisage.lieu_ligne=newValue
      },
    },

   data() {
      return {
          baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/`,
          baseUrlEcole:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
          baseUrlClasse:`${this.$store.state.baseUrl}/api/transport_scolaire/classes/`,
          baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
          baseUrlLieuLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lieulignes/`,
          baseUrlLieuRamassage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieuramassages/`,
          baseUrlItineraire:`${this.$store.state.baseUrl}/api/transport_scolaire/itineraires/`,

      new_eleve: {
            nom: '',
            prenoms: '',
            date_naissance: '',
            ecole: '',
            image:null,
            classe: '',
            ligne: '',
            lieuligne: '',
            lieu_ramassage: '',
            create_lieu_ramassage:false,
            create_classe:false,

            new_lieu_ramassage: {
               lieu_ligne:'',
               ligne:'',
               nom_lieu:''
            },

            new_lieu_remisage: {
               lieu_ligne:'',
               ligne:'',
               nom_lieu:''
            },

            new_classe:{
                nom:'',
            },

            parent:{
                nom_mere:'',nom_pere:'',
                tel_pere:'',tel_mere:''
            },

            type_inscription:'',
            assignationitineraires:[],
            user:{
                 username:"",
                 email:"",
                 password:"",
                 password_verify:"",
                 password_error:"",
                 role:"parent",
            }

        },
  
        ecoles: [],
        classes: [],
        lignes: [],
        lieulignes: [],
        itineraires:[],
        itineraires_filter:[],
        lieulignes_filter : [],
        classes_filter : [],
        lieu_ramassages: [],
        lieu_ramassages_filter:[],
        
        errors:{},
        type_inscriptions:[

            {value:'',text:'--------------------'},
            {value:'ramasse et remisage',text:'Ramasse et Remisage'},
            {value:'ramassage',text:'Ramasse'},
            {value:'remisage',text:'Remisage'},

        ]
           
       };
    },
    methods: {

      handleChangeItineraireCheckBox(itineraire){

          let index=this.new_eleve.assignationitineraires.findIndex((el)=>el.itineraire==itineraire)

          if(index==-1){  

              this.new_eleve.assignationitineraires.push({
                     itineraire:itineraire,
              })

          }else{
              this.new_eleve.assignationitineraires.splice(index,1)
          }

      },

      handleLigneChange(event){

        const ligne=this.lignes.find((el)=>el.pk==event)
        this.lieulignes_filter=ligne.ordrelieus.map((element)=>{   
              return { value: element.lieu_ligne.pk, text : element.lieu_ligne.repr} ;
        })
        // console.log(this.lieulignes_filter)
        //   console.log(event);

        // this.filterItineraire();
        // const lieu_ligne=this.lieulignes.find((el)=>el.pk==event)
        

      },
      filterItineraire(){

        console.log(this.new_eleve.ligne)
            const ligne=this.lignes.find((el)=>el.pk==this.new_eleve.ligne)
            const ecole=this.ecoles.find((el)=>el.pk==this.new_eleve.ecole)

            if(!ligne){
                  return
            }

            if(!ecole){
                  return
            }

            this.itineraires_filter=[];
            console.log(this.itineraires)

            this.itineraires.forEach(element => {

              // verfication si l'ecole exites dans l'iinraire
              const ecole_exist=element.ecoleassignations.find((el)=>el.ecole.repr==ecole.text)
              console.log(ligne,ecole,ecole_exist)
              if(ecole_exist){
                //  alert(element)
                 if (element.ligne==ligne.text ) {
                     this.itineraires_filter.push({...element, value: element.pk, text : element.repr});
                 }

              }
             
            })
      },


      handleLieuLigneChange(event){
          const lieu_ligne=this.lieulignes.find((el)=>el.pk==event)

          this.lieu_ramassages_filter=lieu_ligne.lieuramassages.map((element)=>{
               return { value: element.pk, text : element.repr} 
          })
      },

      handleEcoleChange(event){
        
          const ecole=this.ecoles.find((el)=>el.pk==event)

          this.classes_filter=ecole.classes.map((element)=>{
               return { value: element.pk, text : element.repr} 
          })

          // this.filterItineraire();
      },

      addEleve() {
            // Crez un nouvel objet Project avec les valeurs du formulaire
            // let error="Cette champ est requis"
            // if (!this.new_eleve.nom) {  
            //      this.new_eleve.nom_error = error
            //      return
            // }
            // if (!this.new_eleve.prenoms) {
            //     this.new_eleve.prenoms_error=error
            //     return
            // }
            // if (!this.new_eleve.date_naissance) {
            //     this.new_eleve.date_naissance_error = error
            //     return
            // }
            // if (!this.new_eleve.ecole) {
            //     this.new_eleve.ecole_error = error
            //     return
            // }
            // if (!this.new_eleve.classe) {
            //   this.new_eleve.classe_error = error
            //   return
            // }
            // if (!this.new_eleve.adresse) {
            //   this.new_eleve.adresse_error = error
            //   return
            // }
            // if (!this.new_eleve.ligne) {
            //   this.new_eleve.ligne_error = error
            //   return
            // }
            // if (!this.new_eleve.ligne) {
            //   this.new_eleve.ligne_error = error
            //   return
            // }
            // Convertir l'objet new_eleve en FormData
            if (this.new_eleve)
                   console.log(axios.defaults.headers.common['Authorization'])
            if (this.new_eleve.user.password!=this.new_eleve.user.password_verify) {
                   this.new_eleve.user.password_error = "les deux mots de passes ne se correspondent pas"
                   return
            }else{
                 this.new_eleve.user.password_error =""
            }

            if(this.new_eleve.type_inscription=='ramasse et remisage' && this.new_eleve.assignationitineraires.length!=2){
                 this.errors.type_inscription="le nombre de l'itinraire doit tre egale  deux car le type itineraire est ramasse et remisage"
                 return
            }

            if(this.new_eleve.type_inscription=='ramassage' && this.new_eleve.assignationitineraires.length!=1){
                 this.errors.type_inscription="l enombre de l'itinraire doit tre egale  un car le type itineraire est ramassage"
                 return
            }
 
            if(this.new_eleve.type_inscription=='remisage' && this.new_eleve.assignationitineraires.length!=1){
                 this.errors.type_inscription="l nombre de l'itinraire doit tre egale  un car le type itineraire est remisage"
                 return
            }
            // let a=2;
            // if(a){
            //   console.log(this.new_eleve.assignationitineraires.length,this.new_eleve.type_inscription)
            //       return
            // }
                 


            console.log(this.new_eleve)
            //Envoyer la requte POST  l'API avec les donnes du projet
            
            axios.post(this.baseUrlPage, this.new_eleve)
                 .then(response => {
  
                // Traitez la rponse de l'API en consquence
                console.log(response.data);
                     // Rinitialisez les valeurs du formulaire
                if(!this.$store.state.user){
                       this.login(this.new_eleve.user)
                }else{
                  this.$router.push('/inscription/list');
                }
                
                })
              .catch(error => {
              // Grez les erreurs de la requte
                    console.error(error);
              });
       },

       login(user){
        axios.post(this.$obtainTokenUrl,user)
                .then(response => {
                // Traitez la rponse de l'API en consquence
                console.log(response.data);
                const access=response.data.access
                const refresh=response.data.refresh
                     // Rinitialisez les valeurs du formulaire
                localStorage.setItem('refresh',refresh)
                localStorage.setItem('access',access)
                // login(this.new_eleve.user)
                this.$router.push('/inscription/list');
                
                })
              .catch(error => {
              // Grez les erreurs de la requte
              console.error(error);
              });
       },
       handleImageUploadNewEleve(event){
        const file = event.target.files[0];

        if (file) {
          const reader = new FileReader();
          
          reader.onloadend = () => {
            // Rcuprez les donnes binaires de l'image sous forme de chane base64
            const imageData = reader.result.split(',')[1];
            this.new_eleve.image = imageData;
            // {
            //       name: file.name,
            //       type: file.type,
            //       size: file.size,
            //       data: imageData
            // };
          };
          
          // Lisez le contenu de l'image en tant que donnes binaires
          reader.readAsDataURL(file);
        }
       },
       fecthDataEcole(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    special_detail_manytoone_class:"['classes',]"
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.ecoles = data.map(a => ({...a, value: a.pk, text: a.repr }));
                 
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des ecoles:", error);
                });
          },

          fecthDataClasse(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.classes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des classes:", error);
                });
          },
          fecthDataLigne(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    special_detail_manytoone_class:"['ordrelieus',]"
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lignes = data.map(a => ({...a, value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des lignes:", error);
                });
          },
          fecthDataItineraire(page){
               // Effectuer la requte GET vers l'API en utilisant Axios
               axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                    special_detail_manytoone_class:"['busassignations','ecoleassignations','horaires']"
                  } 
                })
                .then(response => {
                  const data = response.data;
                  this.itineraires = data.results.map((itineraire)=>{
                    
                    if(itineraire.horaires.length>0){

                          itineraire.lieuDepart=itineraire.horaires[0].pointArret	
                          itineraire.heureDepart=itineraire.horaires[0].heureDebut
                          itineraire.lieuArrivee=itineraire.horaires[itineraire.horaires.length-1].pointArret	
                          itineraire.heureArrivee=itineraire.horaires[itineraire.horaires.length-1].heureFin	
                    
                    }else{

                          itineraire.lieuDepart=null
                          itineraire.heureDepart=null
                          itineraire.lieuArrivee=null
                          itineraire.heureArrivee=null
                    }
                   
                    
                   
                    return itineraire
                  })
                  console.log(this.itineraires)
                })
                .catch(error => {
                    console.error("Une erreur s'est produits lors de la rcupration des itineraires:", error);
                });
          },

          fecthDataLieuLigne(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    special_detail_manytoone_class:"['lieuramassages',]"
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieulignes = data.map(a => ({...a, value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des lignes:", error);
                });
          },
          fecthDataLieuRamassage(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieu_ramassages = data.map(a => ({...a, value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des lignes:", error);
                });
          },
  
    },
    mounted() {

       this.fecthDataEcole(this.baseUrlEcole);
       this.fecthDataClasse(this.baseUrlClasse);
       this.fecthDataLigne(this.baseUrlLigne);
       this.fecthDataLieuLigne(this.baseUrlLieuLigne);
       this.fecthDataLieuRamassage(this.baseUrlLieuRamassage);
       this.fecthDataItineraire(this.baseUrlItineraire);

  }




  }
</script>

