
<template>
  <div class="container">
    
    <div class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Detail Inscription</span> <span>Elve</span>
        </h4>
        <div class="line">
        
        </div>
      </div>
      <div class="col-12">

          <b-button type="submit" variant="" @click="valideInscription" class="btn btn-save">
            <font-awesome-icon :icon="['fas', 'floppy-disk']" />
            <span>Valide Inscription</span> 
          </b-button>
          <b-button type="submit" variant="" @click="annulerInscription" class="btn btn-outline-danger text-danger">
            <font-awesome-icon :icon="['fas', 'floppy-disk']" />
            <span>Annuler Inscription</span> 
          </b-button>
      </div>

      <h5>Informations  Elve</h5>
      <div class="col-12 col-md-6">
          <img :src="detail_eleve.image" alt="">
      </div>
      <div class="col-12   col-md-6">
          <div class="table-responsive">
              <div class="table">
                    <tbody>
                        <tr><td>Nom</td> <td>{{detail_eleve.nom}}</td></tr>
                        <tr><td>Prnoms</td> <td>{{detail_eleve.prenoms}}</td></tr>
                        <tr><td>Date de Naissance</td> <td>{{detail_eleve.date_naissance}}</td></tr>
                        <tr><td>Adresse</td> <td>{{detail_eleve.adresse}}</td></tr>
                        <tr>
                          <td>Ecole</td> 
                          <td v-if="detail_eleve.ecole">{{detail_eleve.ecole.repr}}</td>
                          <td v-if="!detail_eleve.ecole">---------------</td>
                        </tr>
                        <tr>
                            <td>Classe</td>
                            <td v-if="detail_eleve.classe">{{detail_eleve.classe.repr}}</td>
                            <td v-if="!detail_eleve.classe">--------------</td>
                          
                        </tr>
                        <tr><td>Adresse</td> <td>{{detail_eleve.adresse}}</td></tr>
                    </tbody>
              </div>
          </div>
      </div>
      
      <div class="col-12   col-md-6">
        <h5>Informations  Parent de l'lve</h5>
          <div class="table-responsive">
              <div class="table">
                    <tbody>
                        <tr><td>Pre</td> <td>{{detail_eleve.parent.nom_pere}}</td></tr>
                        <tr><td>Tel</td> <td>{{detail_eleve.parent.tel_pere}}</td></tr>
                        <tr><td>Mre</td> <td>{{detail_eleve.parent.nom_mere}}</td></tr>
                        <tr><td>Tel</td> <td>{{detail_eleve.parent.tel_mere}}</td></tr>
                    </tbody>
              </div>
          </div>
      </div>
      
      <div class="col-12   col-md-6">
        <h5>Informations  Inscripion </h5>
          <div class="table-responsive">
              <div class="table">
                    <tbody>
                        <tr><td>Ligne</td> <td>{{detail_eleve.ligne.repr}}</td></tr>
                        <tr><td>Lieu Ligne</td> <td>{{detail_eleve.lieu_ramassage.lieu_ligne.repr}}</td></tr>
                        <tr><td>Lieu de Ramassage</td> <td>{{detail_eleve.lieu_ramassage.repr}}</td></tr>
                    </tbody>
              </div>
          </div>
      </div>

      <h5>Itineraire Associ</h5>
      <div class="table-responsive">
          <div v-for="ai in detail_eleve.assignationitineraires" class="itineraire row" :key="ai.pk">
            <div class=""><span> {{ai.itineraire.pk}}</span></div>
            <div class="col-12 col-md-3" v-for="horaire in ai.itineraire.horaires" :key="horaire.pk">
              
                      <div class="">
                          <strong>{{horaire.pointArret.nom_lieu}}</strong>  
                      </div>
                      <div class="">
                        <span> {{horaire.heureDebut}}</span>
                        -
                        <span> {{horaire.heureFin}}</span>
                        
                        </div>
                
                
                      </div>
      
              <div class="col-12 col-md-3">
                <b-form-group label="Bus Associ :">
                  <b-form-select @input="handleSelectBus(ai.pk,ai.itineraire.pk,$event)" v-model="new_bus" :options="buss"></b-form-select>                    </b-form-group>
              </div>
            </div>
      </div>
      
      <h5>Informations  Compte de L'Utilisateur </h5>
      <div class="col-12   col-md-6">
          <div class="table-responsive">
              <div class="table">
                    <tbody>
                        <tr>
                            <td>Nom d'utlisateur</td> 
                            <td v-if="detail_eleve.user">{{detail_eleve.user.username}}</td>
                            <td v-if="!detail_eleve.user">-----------------</td>
                        </tr>
                        <tr>
                          <td>Email</td>
                          <td  v-if="detail_eleve.user">{{detail_eleve.user.email}}</td>
                          <td v-if="!detail_eleve.user">------------------</td>
                        </tr>
                        <tr>
                          <td>Role</td>
                          <td v-if="detail_eleve.user">{{detail_eleve.user.role}}</td>
                          <td v-if="!detail_eleve.user">---------------------</td>
                        </tr>
                    </tbody>
              </div>
          </div>
      </div>  
      <div class="col-12 col-md-6">

        <b-button type="submit" variant="" @click="valideInscription" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Valide Inscription</span> 
        </b-button>
        <b-button type="submit" variant="" @click="annulerInscription" class="btn btn-outline-danger text-danger">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Annuler Inscription</span> 
        </b-button>
    </div> 
    </div>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'EleveEditPage',
  mounted() {
    const encryptedId = this.$route.params.id; // Rcupre l'ID crypt depuis l'URL
    const decryptedId = this.$decryptId(encryptedId); // Dcrypte l'ID
    console.log(decryptedId);

    this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/${decryptedId}/`
    // console.log(url)
    this.fecthData(this.url)
    this.fecthDateBus(this.baseUrlBus);
    // this.fecthDataClasse(this.baseUrlClasse);
    // this.fecthDataLigne(this.baseUrlLigne);
    // this.fecthDataLieuLigne(this.baseUrlLieuLigne);
    // this.fecthDataLieuRamassage(this.baseUrlLieuRamassage);
  },

  data() {
  return {
          
          url:'',
          baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/`,
          baseUrlEcole:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
          baseUrlClasse:`${this.$store.state.baseUrl}/api/transport_scolaire/classes/`,
          baseUrlBus:`${this.$store.state.baseUrl}/api/transport_scolaire/buss/`,
          baseUrlItineraireBusAssignation:`${this.$store.state.baseUrl}/api/ts/assignation_itineraire_bus`,
          baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
          baseUrlLieuLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lieulignes/`,
          baseUrlieuRamassage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieuramassages/`,

          detail_eleve: {
                nom: '',
                prenoms: '',
                date_naissance: '',
                etat:'',
                ecole: {
                  repr:'',
                },
             
                classe:{
                  repr:'',
                },
                ligne: '',
                lieu_ligne: '',
                lieu_ramassage: {
                     repr:'',
                     lieu_ligne: {
                      repr:''
                     }
                },
                parent:{
                    nom_mere:'',nom_pere:'',
                    tel_pere:'',tel_mere:''
                },
                user:{
                    username:'',
                    email:'',
                    role:'',
                }
           },
           
        ecoles: [],
        buss: [],
        classes: [],
        lignes: [],
        lieulignes: [],
        lieuramassages: [],

        assignationitineraires:[],
  };
},
  methods: {
    
    handleSelectBus(pk,id_itineraire,event){


      const idx=this.assignationitineraires.findIndex((el)=>el.itineraire==id_itineraire);
      if(idx==-1){

          const as={
            pk:pk,
            eleve:this.detail_eleve.pk,
            itineraire:id_itineraire,  
            bus:event
          }

          this.assignationitineraires.push(as)

          this.assignationitineraires.find((el)=>el.itineraire==id_itineraire);
      }else{

      const idx=this.assignationitineraires.findIndex((el)=>el.itineraire==id_itineraire);
      this.assignationitineraires[idx].bus=event
      // his.assignationitineraires.find((el)=>el.itineraire==id_itineraire).

      }
     
      // alert(event);
    },
    fecthData(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.detail_eleve =data
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des eleves:", error);
                });
          },

    valideInscription() {
          // Envoyer la requte POST  l'API avec les donnes du projet
          this.detail_eleve.etat="valid"

           console.log(this.assignationitineraires);

           axios.post(this.baseUrlItineraireBusAssignation, this.assignationitineraires)
                .then(response => {
                // Traitez la rponse de l'API en consquence
                console.log(response.data);
                     // Rinitialisez les valeurs du formulaire
     

                axios.put(this.url, {
                    validation:true,
                    etat:"valid",

                    })
                .then(response => {
                  console.log(response.data);
                  this.$router.push('/transport_scolaire/inscription');
                })
                .catch(error => {
                  // Grez les erreurs de la requte
                  console.error(error);
                });


                })
              .catch(error => {
              // Grez les erreurs de la requte
              console.error(error);
              });

          
          }
    ,
        annulerInscription() {
          // Envoyer la requte POST  l'API avec les donnes du projet
          this.detail_eleve.etat="valid"

          axios.put(this.url, {
                    annulation:true,
                    etat:"annul"
                  })
              .then(response => {
                console.log(response.data);
                this.$router.push('/transport_scolaire/inscription');
              })
              .catch(error => {
                 // Grez les erreurs de la requte
                 console.error(error);
              });
          }
    ,
   fecthDataEcole(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.ecoles = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des ecoles:", error);
                });
          },
    
    fecthDateBus(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.buss = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la rcupration des buss:", error);
                });
          },

          fecthDataClasse(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.classes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des classes:", error);
                });
          },
          fecthDataLigne(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lignes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des lignes:", error);
                });
          },
          fecthDataLieuLigne(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieulignes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des lignes:", error);
                });
          },
          fecthDataLieuRamassage(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieuramassages = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des lignes:", error);
                });
          },
  
    },






}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .table-responsive tr >td:first-child{
          font-weight: bold;
  }

.popup-task{
  position: fixed;
  z-index: 100;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 100%;
  max-width: 400px;
  background-color: #383838;
  padding: 20px;
  color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}


.itineraire{
  padding: 3px;

  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.5);
}

</style>

