<template>
  <div>
    
    
    
    <div class="cont-sep">
      <div class="title">
        <h4>
             <span>Horaires et </span> <span>Itineraires</span>
        </h4>
        <div class="line">
         
        </div>
      </div>
      <div class="actions">


      </div>
      <div class="filter-form">
          <div class="item-group">
            <div class="item">
              <label>Ecole:</label>
              <select name="" v-model="ecole_filter" id="">
                <option value="">-------------------</option>
                <option value="{{ecole.pk}}" v-for="ecole in ecoles" :key="ecole.pk">{{ecole.nom}}</option>
              </select>
            </div>
              <div class="item">
                <label>Ligne:</label>
                <select name="" v-model="ligne_filter" id="">
                  <option value="">-------------------</option>
                   <option value="" v-for="ligne in lignes" :key="ligne.pk">{{ligne.nom}}</option>

                </select>
               
              </div>
          </div>
          <font-awesome-icon :icon="['fas', 'filter']" class="icon"/>

      </div>
      <div class="search-form">
         <input type="text" v-model="searchQuery" placeholder="Rechercher  lve ..." />
         <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
      </div>
      <div class="table-responsive">
        <div v-for="itineraire in itineraires" class="itineraire row" :key="itineraire.pk">
                    <div class=""><span> {{itineraire.ligne}}</span></div>
                    <div class="lieu row col-9">


                          <div class="title_ligne">
                            <div class="col-12 col-md-3 text-center">
                                <strong>Ligne</strong>
                            </div>
                            //
                          
                            <div class="col-12 col-md-3 text-center">
                                <strong>Lieux</strong>
                            </div>  
                            <div class="row">

                          
                            <div class="col-12 col-md-3" v-for="horaire in itineraire.horaires" :key="horaire.pk">
                                    <div class="">
                                        <strong>{{horaire.pointArret.nom_lieu}}</strong>  
                                    </div>
                                    <div class="">
                                          <span> {{horaire.heureDebut}}</span>
                                          -
                                          <span> {{horaire.heureFin}}</span>                       
                                    </div>
                            </div>
                          </div>
                         </div>  
                    </div>
                     <div class="col-12 col-md-2">
                        <div class="ecole">
                          <strong>Ecole</strong>  
                        </div>
                       <div class="col-12 col-md-3" v-for="ecoleassignation in itineraire.ecoleassignations" :key="ecoleassignation.pk">
            
                          <div class="">
                              
                              {{ecoleassignation.ecole.nom}}
                          </div>
                   
                      </div>
                     </div>
                    
  </div>
    </div>
     
     <div class="popup-buttons">
          
      </div>
    </div>
    
  </div>
</template>

<script>

import axios from 'axios';

export default {
  name: 'ItinerairePage',
  data() {
    return {
      itineraires: [],
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/itineraires/`,
      baseUrlEcole:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
      baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
      baseUrlItineraire:`${this.$store.state.baseUrl}/api/transport_scolaire/itineraires/`,
      baseUrlHoraire:`${this.$store.state.baseUrl}/api/transport_scolaire/horaires/`,
      baseUrlEcoleAssignation:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoleassignations/`,
      baseUrlBusAssignation:`${this.$store.state.baseUrl}/api/transport_scolaire/busassignations/`,
      baseUrlBus:`${this.$store.state.baseUrl}/api/transport_scolaire/buss/`,
      searchQuery: '',
      startDate: '',
      endDate: '',
      ecole_filter:'',
      ligne_filter:'',
      current_page:2,
      nbr_page:10,
      ecoles:[],
      lignes:[]
      

     
    };
  },
  computed: {
  },
  methods: {
      fecthDataEcole(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{params: {
                paginate:false,
              }
              })
              .then(response => {
                  const data = response.data;
                  this.ecoles = data.map((ecole)=>{
                    return {...ecole,value:ecole.pk,text:ecole.nom}
                  })
                  console.log(this.ecoles)
                })
                .catch(error=>
                {console.error(error)}
                )
               

      },
      // fecthDataItineraire(page){
      //     axios.get(page,{params: {
      //       paginate:false,
      //     }
      //     ).then()
      // ,
      fecthDataLigne(page){
          axios.get(page,{params:{
                 paginate:false,
          }
          }
          )
          .then(response => {
            const data = response.data;
            console.log(data)
            this.lignes = data.map((ligne)=>{
              return {...ligne,value:ligne.pk,text:ligne.nom}
            })
            console.log(this.lignes)
          })
          .catch(error=>
          {console.error(error)}
          )
      },

      fecthData(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    special_detail_manytoone_class:"['busassignations','ecoleassignations','horaires']"
                  } 
                })
                .then(response => {
                  const data = response.data;
                  this.itineraires = data.map((itineraire)=>{
                    
                    if(itineraire.horaires.length>0){
                          itineraire.lieuDepart=itineraire.horaires[0].pointArret	
                          itineraire.heureDepart=itineraire.horaires[0].heureDebut
                          itineraire.lieuArrivee=itineraire.horaires[itineraire.horaires.length-1].pointArret	
                          itineraire.heureArrivee=itineraire.horaires[itineraire.horaires.length-1].heureFin	
                    
                    }else{

                        itineraire.lieuDepart=null
                        itineraire.heureDepart=null
                        itineraire.lieuArrivee=null
                        itineraire.heureArrivee=null
                    }
                   
                    
                   
                    return itineraire
                  })
                  console.log(this.itineraires)
                })
                .catch(error => {
                    console.error("Une erreur s'est produits lors de la rcupration des itineraires:", error);
                });
          },
    },
  mounted() {
     this.fecthData(this.baseUrlPage)
     this.fecthDataEcole(this.baseUrlEcole)
     this.fecthDataLigne(this.baseUrlLigne)

  }
};
</script>

<style scoped>

.itineraire{
  padding: 3px;
  font-size: 13px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.title_ligne{
  border: 1px solid rgba(0, 0, 0, 0.5);
}
</style>