
<template>
  <div class="container">
    
    <form @submit.prevent="editEleve" class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Modification</span> <span>Elve</span>
        </h4>
        <div class="line">
        
        </div>
      </div>

      <h5>Informations  Elve</h5>
      <div class="col-12   col-md-6">
        <b-form-group label="nom :">
          <b-form-input id="nom" v-model="edit_eleve.nom" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Prnom :">
          <b-form-input id="prenoms" v-model="edit_eleve.prenoms" required></b-form-input>
        </b-form-group>
      </div>
    
      <div class="col-12">
        <b-form-group label="Date Naissance :">
          <b-form-input id="date_naissance" v-model="edit_eleve.date_naissance" type="date" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Ecole :">
          <b-form-select v-model="edit_eleve.ecole" :options="ecoles" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Classe :">
          <b-form-select v-model="edit_eleve.classe" :options="classes" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12">
        <b-form-group label="Adresse :">
          <b-form-input v-model="edit_eleve.adresse" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Ligne de bus :">
          <b-form-select v-model="edit_eleve.ligne" :options="lignes" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Lieu de ligne :">
          <b-form-select v-model="edit_eleve.lieuligne" :options="lieulignes" required></b-form-select>
        </b-form-group>
      </div>
    
      <div class="col-12">
        <b-form-group label="Lieu de ramassage :">
          <b-form-select v-model="edit_eleve.lieu_ramassage" :options="lieuramassages" required></b-form-select>
        </b-form-group>
      </div>
      <h5>Informations Parent Elve</h5>
    <div class="col-12 col-md-6">
        <b-form-group label="Nom Pre :">
          <b-form-input v-model="edit_eleve.parent.nom_pere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
    
        <b-form-group label="Tel :">
          <b-form-input v-model="edit_eleve.parent.tel_pere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
        <b-form-group label="Nom Mre :">
          <b-form-input v-model="edit_eleve.parent.nom_mere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
      <b-form-group label="Tel :">
        <b-form-input v-model="edit_eleve.parent.tel_mere" required></b-form-input>
      </b-form-group>
    </div>
    <h5>Itineraire Associ</h5>
    <div class="table-responsive">
        <div v-for="(ai, idx) in edit_eleve.assignationitineraires" class="itineraire row" :key="ai.pk">            <div class=""><span> {{ai.itineraire.pk}}</span></div>
              <div class="col-12 col-md-3" v-for="horaire in ai.itineraire.horaires" :key="horaire.pk">
                
                        <div class="">
                            <strong>{{horaire.pointArret.nom_lieu}}{{idx}}</strong>  
                        </div>
                        <div class="">
                          <span> {{horaire.heureDebut}}</span>
                          -
                          <span> {{horaire.heureFin}}</span>
                          
                          </div>
          
              </div>
      
              <div class="col-12 col-md-3">
                <b-form-group label="Bus Associ :">
                    <template v-if="assignationitineraires[idx]">
                          <b-form-select @input="handleSelectBus(ai.pk,ai.itineraire.pk,$event)" v-model="assignationitineraires[idx].bus" :options="buss"></b-form-select>                
                    </template>
                    <template v-if="!assignationitineraires[idx]">
                          <b-form-select @input="handleSelectBus(ai.pk,ai.itineraire.pk,$event)" v-model="pass" :options="buss"></b-form-select>                
                   </template>
                </b-form-group>
              </div>
          </div>

    </div>
    
     <!-- Itineraire Bus  -->
     <!--<div class="table-responsive">
      <table class="table">
          <thead>
            <tr>

              <th>Cocher</th>
              <th>Lieu Dpart</th>
              <th>Lieu Arrive</th>
              <th>Heure Dpart</th>
              <th>Heure d'arrivi</th>
           
            </tr>
          </thead>
           <transition-group name="itineraire-table" tag="tbody">
            <tr v-for="itineraire in itineraires_filter" :key="itineraire.id">
              <td>
                   <input type="checkbox" name="" @input="handleChangeItineraireCheckBox(itineraire.pk)" id="">
              </td>
              <td v-if="itineraire.lieuDepart">{{ itineraire.lieuDepart.repr }}</td>
              <td v-if="!itineraire.lieuDepart">------------</td>
              <td v-if="itineraire.lieuArrivee">{{ itineraire.lieuArrivee.repr }}</td>
              <td v-if="!itineraire.lieuArrivee">-------------</td>
              <td>{{ itineraire.heureDepart }}<template v-if="!itineraire.heureDepart">-------------</template></td>
              <td>{{ itineraire.heureArrivee }}<template v-if="!itineraire.heureArrivee">-------------</template></td>
            
              <td>
                
              </td>
            </tr>
          </transition-group> 
      </table>
    </div>-->

    <b-button type="submit" variant="" class="btn btn-save">
      <font-awesome-icon :icon="['fas', 'floppy-disk']" />
      <span>Enregistrer les modifications</span> 
    </b-button>
    
    </form>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'EleveEditPage',
  watch: {
      'edit_eleve.ligne': function(newValue, oldValue) {
        oldValue
          //  this.edit_eleve.new_lieu_ramassage.ligne=newValue;
          //  this.edit_eleve.new_lieu_remisage.ligne=newValue
           this.filterItineraire()
      },
      'lignes': function(newValue, oldValue) {
        oldValue
          //  this.edit_eleve.new_lieu_ramassage.ligne=newValue;
          //  this.edit_eleve.new_lieu_remisage.ligne=newValue
           this.filterItineraire()
      },
      'ecoles': function(newValue, oldValue) {
        oldValue
          //  this.edit_eleve.new_lieu_ramassage.ligne=newValue;
          //  this.edit_eleve.new_lieu_remisage.ligne=newValue
           this.filterItineraire()
      },
      'edit_eleve.ecole': function(newValue, oldValue) {
        oldValue
        newValue
          //  this.edit_eleve.new_lieu_ramassage.ligne=newValue;
          //  this.edit_eleve.new_lieu_remisage.ligne=newValue
           this.filterItineraire()
      },
      'edit_eleve.lieuligne': function(newValue, oldValue) {
        oldValue
          //  this.edit_eleve.new_lieu_ramassage.lieu_ligne=newValue
          //  this.edit_eleve.new_lieu_remisage.lieu_ligne=newValue
      },
    },
  mounted() {
        const encryptedId = this.$route.params.id; // Rcupre l'ID crypt depuis l'URL
        const decryptedId = this.$decryptId(encryptedId); // Dcrypte l'ID
        console.log(decryptedId);

        this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/${decryptedId}/`
        // console.log(url)
        this.fecthDataItineraire(this.baseUrlItineraire);
        this.fecthData(this.url)
       
        this.fecthDataClasse(this.baseUrlClasse);

        this.fecthDataLieuLigne(this.baseUrlLieuLigne);
        this.fecthDataLieuRamassage(this.baseUrlLieuRamassage);
        this.fecthDateBus(this.baseUrlBus);
        this.fecthDataLigne(this.baseUrlLigne);
        
       
     },

  data() {
  return {
          
          url:'',
          baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/`,
          baseUrlEcole:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
          baseUrlBus:`${this.$store.state.baseUrl}/api/transport_scolaire/buss/`,
          baseUrlClasse:`${this.$store.state.baseUrl}/api/transport_scolaire/classes/`,
          baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
          baseUrlLieuLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lieulignes/`,
          baseUrlLieuRamassage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieuramassages/`,
          baseUrlItineraireBusAssignation:`${this.$store.state.baseUrl}/api/ts/assignation_itineraire_bus`,
          baseUrlItineraire:`${this.$store.state.baseUrl}/api/transport_scolaire/itineraires/`,

          edit_eleve: {
                nom: '',
                prenoms: '',
                date_naissance: '',
                ecole: '',
                niveau: '',
                classe: '',
                ligne: '',
                lieuligne: '',
                lieu_ramassage: '',
                parent:{
                    nom_mere:'',nom_pere:'',
                    tel_pere:'',tel_mere:''
                },
                assignationitineraires:[],
           },
           
        ecoles: [],
        classes: [],
        buss: [],
        lignes: [],
        itineraires:[],
        lieulignes: [],
        itineraires_filter: [],
        lieuramassages: [],
        assignationitineraires:[],

  };
},
  methods: {

    handleChangeItineraireCheckBox(itineraire){

      let index=this.edit_eleve.assignationitineraires.findIndex((el)=>el.itineraire==itineraire)

      if(index==-1){  

          this.edit_eleve.assignationitineraires.push({
                itineraire:itineraire,
          })

      }else{
          this.edit_eleve.assignationitineraires.splice(index,1)
      }

      },

    filterItineraire(){

            console.log(this.edit_eleve.ligne,this.edit_eleve.ecole);

            // alert(this.edit_eleve.ligne,this.edit_eleve.ecole)
            console.log(this.lignes);

            const ligne=this.lignes.find((el)=>el.pk==this.edit_eleve.ligne)
            const ecole=this.ecoles.find((el)=>el.pk==this.edit_eleve.ecole)
            console.log("pass",ligne,ecole)
            if(!ligne){
                  return
            }

            if(!ecole){
                  return
            }

            this.itineraires_filter=[];
            console.log(this.itineraires)

            this.itineraires.forEach(element => {

              // verfication si l'ecole exites dans l'iinraire
              const ecole_exist=element.ecoleassignations.find((el)=>el.ecole.repr==ecole.text)
              console.log(ligne,ecole,ecole_exist)
              if(ecole_exist){
                //  alert(element)
                if (element.ligne==ligne.text ) {
                    this.itineraires_filter.push({...element, value: element.pk, text : element.repr});
                }

              }
            
            })
        },
    handleSelectBus(pk,id_itineraire,event){


        const idx=this.assignationitineraires.findIndex((el)=>el.itineraire==id_itineraire);
        if(idx==-1){

            const as={
              pk:pk,
              eleve:this.detail_eleve.pk,
              itineraire:id_itineraire,  
              bus:event
            }
            as
            this.assignationitineraires.push(as)

            // this.assignationitineraires.find((el)=>el.itineraire==id_itineraire);
        }else{

        const idx=this.assignationitineraires.findIndex((el)=>el.itineraire==id_itineraire);
        idx
        this.assignationitineraires[idx].bus=event
        // his.assignationitineraires.find((el)=>el.itineraire==id_itineraire).

        }

        // alert(event);
    },
    fecthDataItineraire(page){
               // Effectuer la requte GET vers l'API en utilisant Axios
               axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                    special_detail_manytoone_class:"['busassignations','ecoleassignations','horaires']"
                  } 
                })
                .then(response => {
                  const data = response.data;
                  this.itineraires = data.results;
                  this.fecthDataEcole(this.baseUrlEcole);
                  console.log(this.itineraires)
                })
                .catch(error => {
                    console.error("Une erreur s'est produits lors de la rcupration des itineraires:", error);
                });
          },

    valideInscription() {
          // Envoyer la requte POST  l'API avec les donnes du projet
          this.detail_eleve.etat="valid"

           console.log(this.assignationitineraires);

           axios.post(this.baseUrlItineraireBusAssignation, this.assignationitineraires)
                .then(response => {
                // Traitez la rponse de l'API en consquence
                console.log(response.data);
                     // Rinitialisez les valeurs du formulaire
     

                axios.put(this.url, {
                    validation:true,
                    etat:"valid",

                    })
                .then(response => {
                  console.log(response.data);
                  this.$router.push('/transport_scolaire/inscription');
                })
                .catch(error => {
                  // Grez les erreurs de la requte
                  console.error(error);
                });


                })
              .catch(error => {
              // Grez les erreurs de la requte
              console.error(error);
              });

          
    },
    fecthDateBus(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.buss = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la rcupration des buss:", error);
                });
          },

    fecthData(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;

                   data.assignationitineraires.forEach(element => {
                    
                    let bus=null
                    if(element.bus){
                         bus=element.bus.pk
                    }else{
                      bus=element.bus
                    }
                    const as={
                              pk:element.pk,
                              eleve:element.eleve.pk,
                              itineraire:element.itineraire.pk,  
                              bus:bus
                          }
                    this.assignationitineraires.push(as)
                    });

                   this.edit_eleve = {
                    ...data,
                    ecole: data.ecole.pk,
                    classe: data.classe.pk,
                    ligne: data.ligne.pk,
                    lieu_ramassage: data.lieu_ramassage.pk,
                    lieuligne: data.lieu_ramassage.lieu_ligne.pk,
                    // user:user,
                    // image:image
                  };
                  delete this.edit_eleve.user;
                  delete this.edit_eleve.image;

                 
                  
                 
                                
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des eleves:", error);
                });
          },
    editEleve() {


          // Crez un nouvel objet Project avec les valeurs du formulaire
          // Envoyer la requte POST  l'API avec les donnes du projet
          axios.put(this.url, this.edit_eleve)
              .then(response => {

                response
                axios.post(this.baseUrlItineraireBusAssignation, this.assignationitineraires)
                .then(response => {
                    console.log(response)
                    // Traitez la rponse de l'API en consquence
                    console.log(response.data);
                    this.$router.push('/transport_scolaire/eleve/');
                })
              .catch(error => {
              // Grez les erreurs de la requte
              console.error(error);
              });
             

              })
              .catch(error => {
              // Grez les erreurs de la requte
              console.error(error);
              });
    
    
 
     }

 ,
  fecthDataEcole(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.ecoles = data.map(a => ({...a, value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des ecoles:", error);
                });
          },
          fecthDataClasse(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.classes = data.map(a => ({...a, value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des classes:", error);
                });
          },
          fecthDataLigne(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lignes = data.map(a => ({...a, value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la rcupration des lignes:", error);
                });
          },
          fecthDataLieuLigne(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieulignes = data.map(a => ({...a, value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des lignes:", error);
                });
          },
          fecthDataLieuRamassage(page){
              // Effectuer la requte GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieuramassages = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la rcupration des lieu de ramassages:", error);
                });
          },
  
    },






}
</script>

